.linear-table {
  table {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;

    border-radius: 2px;
    border-collapse: collapse;
    width: 100%;
    td,
    th {
      border: 1px solid #ddd;
      padding: 8px;
    }
    tr:nth-child(even) {
      background-color: #f2f2f2;
    }
    tr:hover {
      background-color: #ddd;
    }
    th {
      padding-top: 12px;
      padding-bottom: 12px;
      background-color: #4caf50;
      color: white;
    }
    .question-title {
      text-align: left;
    }
    .answer-title {
      width: 80%;
      text-align: left;
    }
  }
}
