.answer-card {
  padding: 2px 0 0 5px;
  margin: 5px;
  text-align: justify;
  border: grey solid 0.5px;
  border-radius: 2px;
  position: relative;
}

.invisible {
  background: repeating-linear-gradient(-60deg, #555 0, #555 0.5px, transparent 1px, transparent 6px);
}

.answer-code {
  font-size: 12px;
  color: black;
  font-weight: 700;
}
.answer-title {
  font-size: 14px;
}
.answer-extend {
  position: absolute;
  top: 0;
  right: 10px
}